			</div>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon.js"
	import cloudApp from "@/config/cloudapp"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {

			}
		},
		methods: {
			onWxLogin() {
				var that = this;
				cloudApp.wxLogin();
			},
			onLoad() {
				var that = this;
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

